import { useEffect, useState } from 'react';
import Content from '../../components/Content/Content';
import PopularQuestions from '../../components/PopularQuestions/PopularQuestions';
import Sidebar from '../../components/Sidebar/Sidebar';
import ContentHeaderItems from './components/ContentHeaderItems/ContentHeaderItems';
import ContentItems from './components/ContentItems/ContentItems';
import {getQuestion} from "../../services/getQuestions";
import { useParams } from 'react-router-dom';

const Question = ( ) => {
    const [data, setData] = useState({_id: "", Tittle: "Loading...", Description: "", Tags: [], Replies: [], Votes: []});

    const {_id} = useParams();

    useEffect( () => {
        const fetchData = async () =>
        {
            let res = await getQuestion(_id);
            setData(res);
        }
        fetchData();
    }, [_id])



    return (
        <>
            <section className="contenido">
                <Sidebar/>
                <Content ContentHeaderItems={ContentHeaderItems} ContentItems={ContentItems} data={data} Tittle={data.Tittle}/>
                <PopularQuestions/>
            </section>
        </>
    )   
}

export default Question;