const urlbase = 'https://simpleclonstackoverflowbackend.herokuapp.com';
const questionsLink = `${urlbase}/getquestions`;
const questionLink = `${urlbase}/getquestion`;

export const getQuestions = async() => {
    
    let data = await fetch(questionsLink, { method:'post' });
    let json = await data.json();
    return json;
}

export const getQuestion = async(_id) => {
    let data = await fetch(questionLink, { method:'post', headers: { 'Content-Type': 'application/json' }, body: JSON.stringify({ _id:_id }) });
    let json = await data.json();
    return json;
}
