import decodeToken from './JsonWebToken';

const urlbase = 'https://simpleclonstackoverflowbackend.herokuapp.com';

const isLogged = async () => 
{
    const token = await localStorage.getItem('token');
    if (!token) return false;
    const decode = await decodeToken(token);

    let isExpired = await ( decode.exp  - Math.floor(Date.now() / 1000))
    
    return ( isExpired > 0 ? true : false );
}

const RegisterAccount = async (Email, Name, Password) =>
{
    const data = await fetch(`${urlbase}/register`, {
        method: 'post',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify( { Email, Name, Password } )
    });
    const json = await data.json();
    if ( json.err ) return json.err;
 
    localStorage.setItem('token', json.token );
    return true;
}

const LogInAccount = async (Email, Password) =>
{
    const data = await fetch(`${urlbase}/login`, {
        method: 'post',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify( { Email, Password } )
    });
    const json = await data.json();
    if ( json.err ) return json.err;
 
    localStorage.setItem('token', json.token );
    return true;
}

const FetchPost = async (url, data) =>
{

    const token = await localStorage.getItem('token');
    if (!token) return;
    const res = await fetch(url+"", {
        headers:{
            'Content-Type': 'application/json',
            'authorization': `bearer ${token}`
        },
        method: "POST",
        body: JSON.stringify(data),
    });
    const json = await res.json();
    return json;
}

const getUserId = async () => 
{
    const token = await localStorage.getItem('token');
    if (!token) return 0;
    const decode = await decodeToken(token);
    return decode["id"];
}
const exportFunctions = ({
    LogInAccount,
    RegisterAccount,
    isLogged,
    FetchPost,
    getUserId
});
export default exportFunctions;

