import React, { useState, useEffect, useRef } from 'react';
import { Redirect } from 'react-router-dom';
import authService from '../../services/authService';
import Modal from "../../components/Modal/Modal";
import './Register.scss';
const Register = () => {
    
    const Name = useRef(), Email = useRef(), Password = useRef(), ConfirmPassword = useRef();
    const [isLogged, setisLogged]   = useState(false);
    const [sucess, SetSucess ]      = useState(false);
    const [ isSending, setSending ] = useState(false);
    const [err, setErr]             = useState(null);

    useEffect( () => {
        const getInfo = async()=>{
            let log = await authService.isLogged();
            setisLogged(log);
        }

        getInfo();
    }, [])

    const submitReg = async (e) => {
        e.preventDefault();
        if ( Password.current.value !== ConfirmPassword.current.value || isSending || sucess ) return;
        setSending(true);
        let data = await authService.RegisterAccount(Email.current.value, Name.current.value, Password.current.value);
        setSending(false);
        data === true ? SetSucess(data) : setErr(data);
    }

    const validatePasswords = () => {
        setErr(null);
        if ( Password.current.value !== ConfirmPassword.current.value ) return setErr("las contraseñas no coinciden");
    }

    return(
        <>
        {
            sucess ? <Modal hideIn={5000} tittle="¡Registrado!" message="¡Tu cuenta ha sido registrada correctamente!" redirectPath="/" /> : null
        }

        { 
            isLogged ? <Redirect to="/" /> : 
            <>
                <form class="regContent" onSubmit={submitReg} >
                    <img src="./assets/images/logo.png" alt="logo"/>
                    <div class="card">
                        
                        <label htmlFor="name">Nombre de usuario</label> 
                        <input ref={Name} id="name" type="text"/>
                        <label  htmlFor="email">Correo</label>
                        <input ref={Email} id="email" type="email"/>
                        <label htmlFor="password" >Contraseña</label>
                        <input ref={Password} id="password" type="password" onKeyUp={validatePasswords} />
                        <label htmlFor="confirm" >Confirmar contraseña</label>
                        <input ref={ConfirmPassword} id="confirm" onKeyUp={validatePasswords} type="password"/>
                        <label>{err ?? null}</label>

                        <button class="bluebutton">Registrarse</button>
                    </div>
                </form>
            </>
        }
        </>
    )
}

export default Register;