import { TagsTypes as TYPES } from "../actions/Tags";

const initialState = {
    tags: []
}

const reducerTags = ( state = initialState, action = { type: '-' } ) => {

    switch ( action.type ){
        case TYPES.ADDTAG:
            let alreadyExist = state.tags.find( el => el === action.tag );
            
            if ( !alreadyExist )
            {
                state.tags.push(action.tag);
                return { tags: state.tags };
            }
            return state;
            
        case TYPES.DELTAG:
            let newtags = state.tags.filter( el => el !== action.tag );
            return { tags: newtags };

        default:
            return state;
    }
    
}

export default reducerTags;